<template>
    <div class="header" :class=" $store.state.showSideBar ? 'smallHeader' :'bigHeader'">
        <h3>学校实验室仓管系统</h3>
        <div class="drop_out" @click="dropOut()">
            <i class="iconfont el-icon-pharmaceutical_administration-tuichu"></i>
            <span>退出</span>
        </div>
    </div>
</template>
<script>
	export default {
		data() {
			return {

			};
		},
		created() {
		},
		methods: {
            a(val){
                //在home.vue中测试父子通信
//                console.log(val)
            },
			dropOut() {
                this.$router.push('/')
//                localStorage.removeItem('user')
                localStorage.clear();
            },
        },
	};
</script>
<style lang="less" rel="stylesheet/less">
    .bigHeader{
        padding-left: 71px;
    }
    .smallHeader{
        padding-left: 202px;
    }
    .header {
        transition: all .8s;
        /*min-width: 1200px;*/
        box-sizing: border-box;
        width: 100%;
        height: 80px;
        border-bottom: 1px solid #ddd;
        h3{
            height: 80px;
            line-height: 80px;
            margin-left: 30px;
            font-family: PingFangSC;
            font-size: 26px;
            font-weight: 600;
            text-align: left;
            color: #223241;
            float: left;
        }
        .drop_out{
            float: right;
            height: 80px;
            line-height: 80px;
            margin-right: 50px;
            cursor: pointer;
            i {
                display: inline-block;
                height: 80px;
                line-height: 80px;
                &:before{
                    font-size: 26px;
                }
            }
            span {
                font-size: 16px;
                float: right;
                margin-left: 5px;
            }
        }
    }
</style>



