<template>
    <div class="sidebar">
        <div class="Telescopic" :style="isCollapse ? '' : ''">
            <span class="el-icon-d-arrow-left" v-if="isCollapse" @click="isCollapse = !isCollapse"></span>
            <span class="el-icon-d-arrow-right" v-if="!isCollapse" @click="isCollapse = !isCollapse"></span>
        </div>
        <div class="perInformation" v-show="isCollapse">
            <img src="../../assets/user_account_picture.png" alt="" width="70px" height="70px">
            <h3>{{ name }}</h3>
            <p></p>
        </div>
        <el-menu :default-active="onRoutes" class="el-menu-vertical-demo" router :collapse="!isCollapse" :unique-opened="true">
            <el-menu-item :class="$store.state.sideIndex == 1? 'isLight': ''" v-if="isAdmin == 0" index="homepage" route="/homepage">
                <i class="iconfont el-icon-pharmaceutical_administration-cangku"></i>
                <span slot="title">库存管理</span>
            </el-menu-item>
            <el-menu-item v-if="isAdmin == 0" index="operation" route="/operation">
                <i class="iconfont el-icon-pharmaceutical_administration-ruku"></i>
                <span slot="title">入库操作</span>
            </el-menu-item>
            <el-menu-item v-if="isAdmin == 0" index="received" route="/received">
                <i class="iconfont el-icon-pharmaceutical_administration-kuaisulingyong1"></i>
                <span slot="title">快速领用</span>
            </el-menu-item>
            <el-menu-item v-if="isAdmin == 0" index="return" route="/return">
                <i class="iconfont  el-icon-pharmaceutical_administration-guihuan"></i>
                <span slot="title">快速归还</span>
            </el-menu-item>
            <el-menu-item v-if="isAdmin == 0" index="records" route="/records">
                <i class="iconfont el-icon-pharmaceutical_administration-jilu"></i>
                <span slot="title">记录管理</span>
            </el-menu-item>
            <el-menu-item v-if="isAdmin == 1" index="manifest" route="/manifest">
                <i class="iconfont el-icon-pharmaceutical_administration-baomingqingdan"></i>
                <span slot="title">添加清单</span>
            </el-menu-item>
