                <!--<el-submenu :index="item.index"  v-if="item.subs">-->
                    <!--<template slot="title" >-->
                        <!--<i :class="item.icon1"></i>-->
                        <!--<span slot="title">{{ item.title }}</span>-->
                    <!--</template>-->
                    <!--<el-menu-item-group v-if="item.subs">-->
                        <!--<el-menu-item :index="subItem.index" v-for="(subItem,i) in item.subs" :key="i" :route="subItem.href">{{ subItem.title }}</el-menu-item>-->
                    <!--</el-menu-item-group>-->
                <!--</el-submenu>-->
                <!--<el-menu-item :index="item.index" v-if="!item.subs" :route="item.href" >-->
                    <!--<i :class="item.icon1"></i>-->
                    <!--<span slot="title">{{ item.title }}</span>-->
                <!--</el-menu-item>-->
            <!--</template>-->
        </el-menu>
    </div>
</template>
<script>
	export default {
		data() {
			return {
                name: '',
				isCollapse: true,
                isAdmin: 1,         //权限
//                sideItems:[
//					{
//						icon1: "",
//						index: "homepage",
//						title: "库存管理",
//						href: {
//							path: "/homepage",
//							component: resolve => require(["../HomePage.vue"], resolve)
//						}
//					},
//					{
//						icon1: "",
//						index: "received",
//						title: "快速领用",
//						href: {
//							path: "/received",
//							component: resolve => require(["../Received.vue"], resolve)
//						}
