<style lang="less" rel="stylesheet/less">
    .sidebar ul.el-menu .isLight{
        background-color: #5ca8e7!important;
    }
    .sidebar {
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        height: 100%;
        overflow: auto;
        /*overflow-x: hidden;*/
        z-index: 100;
        background-color: #223241;
        //伸缩按钮
        .Telescopic{
            color: #fff;
            position: absolute;
            top: 0;
            left: 50%;
            width: 50px;
            margin-left: -25px;
            text-align: center;
            font-size: 20px;
            z-index: 99999999;
            height: 50px;
            line-height: 50px;
            span{
                cursor: pointer;
            }
        }
        //菜单栏
        ul.el-menu{
            background-color: transparent;
            border-right: 0;
            margin-top: 35px;
            //li
            .el-menu-item{
                height: 80px;
                line-height: 80px;
                color: #fff;
                i{
                    color: #fff;
                    font-size: 30px;
                }
                span{
                    height: 25px;
                    font-family: PingFangSC;
                    font-size: 18px;
                    text-align: left;
                    margin-left: 14px;
                }
            }
            .is-active{
                background-color: #5ca8e7!important;
            }
            .el-menu-item:focus, .el-menu-item:hover{
                background-color: transparent;
            }
            .is-active:hover{
                background-color: #5ca8e7!important;
            }
        }
        //伸缩   展开
        .el-menu-vertical-demo:not(.el-menu--collapse) {
            width: 202px;
            min-height: 550px;
            //入库
            .iconfont.el-icon-pharmaceutical_administration-ruku {
                font-size: 24px;
                margin-left: 3px;
                margin-right: 3px;
            }
            //记录
            .iconfont.el-icon-pharmaceutical_administration-jilu {
                font-size: 26px;
                margin-left: 2px;
            }
        }
        .el-menu--collapse {
            width: 71px;
            //入库
            .iconfont.el-icon-pharmaceutical_administration-ruku {
                font-size: 24px;
                margin-left: 3px;
            }
            //记录
            .iconfont.el-icon-pharmaceutical_administration-jilu {
                font-size: 26px;
                margin-left: 2px;
            }
        }
        //个人信息头像
        .perInformation{
            margin-top: 50px;
            background-color: #223241;
            text-align: center;
            color: #fff;
            h3 {
                font-weight: normal;
            }
        }
    }
</style>




