<template>
    <div class="wrapper">
        <v-head ref="aa"></v-head>
        <v-sidebar></v-sidebar>
        <div :class=" $store.state.showSideBar ? 'content' :'contentBig'">
            <div class="Minimal">
                <div style="width: 100%;height: 100%;background-color: #fff">
                    <transition name="move" mode="out-in"><router-view></router-view></transition>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
	import vHead from './Header.vue';
	import vSidebar from './Sidebar.vue';

	export default {
		components:{
			vHead, vSidebar
		},
		mounted () {
		    this.$refs.aa.a('wwj')
		}
	}
</script>
<style rel="stylesheet/less">
    *{
        margin:0;
        padding:0;
    }
    body{
        width: 100%;
        height: 100%;
    }
    #app, .wrapper, html {
        width: 100%;
        height: 100%;
        /*overflow: hidden;*/
    }
    .content {
        background: none repeat scroll 0 0 #f1f1f1;
        position: absolute;
        right: 0;
        top: 80px;
        bottom: 0;
        padding-left: 202px;
        width: 100%;
        box-sizing: border-box;
        overflow-y: auto;
        transition: all .8s;
    }
    .contentBig {
        background: none repeat scroll 0 0 #f1f1f1;
        position: absolute;
        right: 0;
        top: 80px;
        bottom: 0;
        padding-left: 71px;
        width: 100%;
        box-sizing: border-box;
        overflow-y: auto;
        transition: all 0.8s;
    }
    .Minimal{
        min-width: 1214px;
        padding: 12px;
        height: 100%;
    }
</style>



